import React, { useState, useEffect } from 'react';
import { useQuery } from '@tanstack/react-query';
import {
    Card,
    CardContent,
    Grid,
    Typography,
    CircularProgress,
    Alert,
    Box,
    TextField,
    Paper,
    IconButton,
    Tooltip,
    ToggleButton,
    ToggleButtonGroup,
    Accordion,
    AccordionSummary,
    AccordionDetails,
    Link,
    Tabs,
    Tab,
} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import RefreshIcon from '@mui/icons-material/Refresh';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CloseIcon from '@mui/icons-material/Close';
import WeatherTimePlot from './WeatherTimePlot';
import { fetchWeatherData, searchLocation } from '../api/weatherApi';
import { Location } from '../types/weather';
import { useTheme } from '../context/ThemeContext';
import { formatTime, formatDate, formatDayOfWeek } from '../utils/timeUtils';
import { getNWSForecastUrl } from '../utils/weatherUtils';
import { getPrecipitationAmount, getRelativeHumidityText, getWindDirectionArrow, getCloudCoverText, getWindSpeedText, getPrecipitationType, getLiftedIndexText, getSnowDepthText, getSeeingType, getTransparencyType } from '../utils/textDescriptions';
import { getWeatherIcon } from '../utils/textDescriptions';

const defaultLocation: Location = {
    lat: 47.6062,
    lng: -122.3321,
    name: 'Seattle, WA'
};

const Weather: React.FC = () => {
    const { isDarkMode, toggleTheme } = useTheme();
    const [location, setLocation] = useState<Location>(() => {
        // Try to load saved location from localStorage
        const savedLocation = localStorage.getItem('lastLocation');
        if (savedLocation) {
            try {
                return JSON.parse(savedLocation);
            } catch (e) {
                console.error('Error parsing saved location:', e);
            }
        }
        return defaultLocation;
    });
    const [searchQuery, setSearchQuery] = useState('');
    const [, setIsSearching] = useState(false);
    const [isGettingLocation, setIsGettingLocation] = useState(false);
    const [temperatureUnit, setTemperatureUnit] = useState<'C' | 'F'>(() => {
        const savedUnit = localStorage.getItem('temperatureUnit');
        return (savedUnit === 'F' || savedUnit === 'C') ? savedUnit : 'C';
    });
    const [isRefreshing, setIsRefreshing] = useState(false);
    const [formattedLastUpdated, setFormattedLastUpdated] = useState<Date>(new Date());
    const [selectedDateIndex, setSelectedDateIndex] = useState(0);

    // Save location to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('lastLocation', JSON.stringify(location));
    }, [location]);

    // Save temperature unit to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('temperatureUnit', temperatureUnit);
    }, [temperatureUnit]);

    const convertTemperature = (celsius: number): number => {
        if (temperatureUnit === 'F') {
            return (celsius * 9 / 5) + 32;
        }
        return celsius;
    };

    const { data: weatherResponse, isLoading: isLoadingWeather, error, refetch } = useQuery({
        queryKey: ['weather', location],
        queryFn: async () => {
            const { data, date } = await fetchWeatherData(location);
            return { data, date };
        },
        refetchInterval: 1000 * 60 * 60, // Refetch every hour
    });

    useEffect(() => {
        const updateFormattedTimes = async () => {
            if (!weatherResponse) return;

            // Update last updated time
            setFormattedLastUpdated(weatherResponse.date);
        };
        updateFormattedTimes();
    }, [weatherResponse, location]);

    const handleRefresh = async () => {
        setIsRefreshing(true);
        try {
            await refetch();
        } catch (error) {
            console.error('Error refreshing weather data:', error);
        } finally {
            setIsRefreshing(false);
        }
    };

    const handleGetCurrentLocation = () => {
        if (!navigator.geolocation) {
            alert('Geolocation is not supported by your browser. Please try searching for a location instead.');
            return;
        }

        setIsGettingLocation(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                setLocation({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                    name: 'Current Location'
                });
                setIsGettingLocation(false);
                setSearchQuery(`${position.coords.latitude.toFixed(4)},${position.coords.longitude.toFixed(4)}`);
            },
            (error) => {
                console.error('Error getting location:', error);
                let errorMessage = 'Error getting your location. ';

                switch (error.code) {
                    case error.PERMISSION_DENIED:
                        errorMessage += 'Please enable location services in your browser settings and try again.';
                        break;
                    case error.POSITION_UNAVAILABLE:
                        errorMessage += 'Location information is unavailable. Please try searching for a location instead.';
                        break;
                    case error.TIMEOUT:
                        errorMessage += 'The request to get your location timed out. Please try again.';
                        break;
                    default:
                        errorMessage += 'Please try searching for a location instead.';
                }

                alert(errorMessage);
                setIsGettingLocation(false);
            },
            {
                enableHighAccuracy: true,
                timeout: 10000,
                maximumAge: 0
            }
        );
    };

    const handleSearch = async () => {
        if (!searchQuery.trim()) return;

        setIsSearching(true);
        try {
            const data = await searchLocation(searchQuery);

            if (data && data.length > 0) {
                setLocation({
                    lat: parseFloat(data[0].lat),
                    lng: parseFloat(data[0].lon),
                    name: data[0].display_name
                });
            } else {
                alert('Location not found. Please try a different search term.');
            }
        } catch (error) {
            console.error('Error geocoding location:', error);
            alert('Error searching for location. Please try again.');
        } finally {
            setIsSearching(false);
        }
    };

    const handleClearSearch = () => {
        setSearchQuery('');
    };

    return (
        <Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h4">
                    Weather
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <ToggleButtonGroup
                        value={temperatureUnit}
                        exclusive
                        onChange={(_, newUnit) => newUnit && setTemperatureUnit(newUnit)}
                        size="small"
                    >
                        <ToggleButton value="C">°C</ToggleButton>
                        <ToggleButton value="F">°F</ToggleButton>
                    </ToggleButtonGroup>
                    <Tooltip title={`Switch to ${isDarkMode ? 'light' : 'dark'} mode`}>
                        <span>
                            <IconButton onClick={toggleTheme} color="inherit">
                                {isDarkMode ? <Brightness7Icon /> : <Brightness4Icon />}
                            </IconButton>
                        </span>
                    </Tooltip>
                </Box>
            </Box>

            <Box sx={{ mb: 4 }}>
                <Grid container spacing={2} alignItems="center">
                    <Grid item xs>
                        <TextField
                            fullWidth
                            label="Search Location"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
                            placeholder="Enter a city, address, or landmark"
                            inputProps={{ enterKeyHint: 'search' }}
                            InputProps={{
                                startAdornment: (
                                    <Tooltip title="Use Current Location">
                                        <span>
                                            <IconButton
                                                onClick={handleGetCurrentLocation}
                                                disabled={isGettingLocation}
                                                color="primary"
                                                size="small"
                                                sx={{ mr: 1 }}
                                            >
                                                {isGettingLocation ? (
                                                    <CircularProgress size={20} />
                                                ) : (
                                                    <LocationOnIcon />
                                                )}
                                            </IconButton>
                                        </span>
                                    </Tooltip>
                                ),
                                endAdornment: searchQuery && (
                                    <Tooltip title="Clear Search">
                                        <IconButton
                                            onClick={handleClearSearch}
                                            size="small"
                                            sx={{ mr: 1 }}
                                        >
                                            <CloseIcon />
                                        </IconButton>
                                    </Tooltip>
                                ),
                            }}
                        />
                    </Grid>
                </Grid>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                    Current Location: (
                    <a
                        href={`https://www.google.com/maps?q=${location.lat},${location.lng}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: 'inherit', textDecoration: 'underline' }}
                    >
                        {location.name}
                    </a>
                    )
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" color="text.secondary">
                        Last updated: {formattedLastUpdated.toUTCString()}
                    </Typography>
                    <Tooltip title="Refresh Weather Data">
                        <span>
                            <IconButton
                                onClick={handleRefresh}
                                disabled={isRefreshing || isLoadingWeather}
                                color="primary"
                                size="small"
                            >
                                {isRefreshing ? <CircularProgress size={20} /> : <RefreshIcon />}
                            </IconButton>
                        </span>
                    </Tooltip>
                </Box>
            </Box>

            {error ? (
                <Alert severity="error">
                    Error loading weather data: {error instanceof Error ? error.message : 'Unknown error'}
                </Alert>
            ) : isLoadingWeather ? (
                <Box display="flex" justifyContent="center" alignItems="center" minHeight="300px">
                    <CircularProgress />
                </Box>
            ) : (
                <>
                    <Paper elevation={2} sx={{ p: 3, mb: 4 }}>
                        {weatherResponse && (
                            <>
                                <WeatherTimePlot
                                    data={weatherResponse.data}
                                    temperatureUnit={temperatureUnit}
                                    date={weatherResponse.date}
                                    location={location}
                                />
                            </>
                        )}

                    </Paper>

                    {weatherResponse && (() => {
                        // Group weather data by date
                        const groupedByDate = weatherResponse.data.reduce((acc: Record<string, typeof weatherResponse.data>, data) => {
                            const date = formatDate(data.localDate);
                            if (!acc[date]) {
                                acc[date] = [];
                            }
                            acc[date].push(data);
                            return acc;
                        }, {});

                        // Sort dates
                        const sortedDates = Object.keys(groupedByDate).sort((a, b) => {
                            return new Date(a).getTime() - new Date(b).getTime();
                        });

                        const handleDateChange = (event: React.SyntheticEvent, newValue: number) => {
                            setSelectedDateIndex(newValue);
                        };

                        return (
                            <>
                                <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
                                    <Tabs
                                        value={selectedDateIndex}
                                        onChange={handleDateChange}
                                        variant="scrollable"
                                        scrollButtons="auto"
                                        allowScrollButtonsMobile
                                    >
                                        {sortedDates.map((date, index) => (
                                            <Tab
                                                key={date}
                                                label={
                                                    <Box>
                                                        <Typography variant="body1">
                                                            {formatDayOfWeek(new Date(date))}
                                                        </Typography>
                                                        <Typography variant="caption" color="text.secondary">
                                                            {formatDate(new Date(date))}
                                                        </Typography>
                                                    </Box>
                                                }
                                            />
                                        ))}
                                    </Tabs>
                                </Box>

                                <Grid container spacing={3}>
                                    {groupedByDate[sortedDates[selectedDateIndex]].map((data) => (
                                        <Grid item xs={12} sm={6} md={4} key={data.timepoint}>
                                            <Card
                                                sx={{
                                                    height: '100%',
                                                    transition: 'transform 0.2s',
                                                    '&:hover': {
                                                        transform: 'translateY(-4px)',
                                                        boxShadow: 3,
                                                    },
                                                }}
                                            >
                                                <CardContent>
                                                    <Typography variant="h5" component="h2" gutterBottom>
                                                        {formatTime(data.localDate)}
                                                    </Typography>
                                                    <Typography variant="h2" component="p" color="primary" gutterBottom>
                                                        {getWeatherIcon(data.weather)}
                                                    </Typography>
                                                    <Typography variant="h5" component="p" gutterBottom>
                                                        {convertTemperature(data.temp2m).toFixed(1)}°{temperatureUnit}
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        <Typography component="span" fontWeight="bold">Cloud Cover:</Typography> {getCloudCoverText(data.cloudcover)}
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        <Typography component="span" fontWeight="bold">Humidity:</Typography> {getRelativeHumidityText(data.rh2m)}
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        <Typography component="span" fontWeight="bold">Wind:</Typography> {getWindSpeedText(data.wind10m.speed)} {' '}
                                                        <span style={{ display: 'inline-block', transform: `rotate(${Number(data.wind10m.direction) - 90}deg)`, transformOrigin: 'center' }}>{getWindDirectionArrow()} </span>
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        <Typography component="span" fontWeight="bold">Precipitation:</Typography> {getPrecipitationType(data.prec_type)} ({getPrecipitationAmount(data.prec_amount)})
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        <Link href="https://en.wikipedia.org/wiki/Lifted_index#" target="_blank" rel="noopener noreferrer" color="inherit" underline="always">
                                                            <Typography component="span" fontWeight="bold">Lifted Index</Typography>
                                                        </Link>
                                                        <Typography component="span" fontWeight="bold">{': '}</Typography>
                                                        {getLiftedIndexText(data.lifted_index)}
                                                    </Typography>
                                                    {data.msl_pressure && (
                                                        <Typography variant="body2" color="text.secondary">
                                                            <Typography component="span" fontWeight="bold">Pressure:</Typography> {data.msl_pressure} hPa
                                                        </Typography>
                                                    )}
                                                    {typeof data.snow_depth !== 'undefined' && (
                                                        <Typography variant="body2" color="text.secondary">
                                                            <Typography component="span" fontWeight="bold">Snow Depth:</Typography> {getSnowDepthText(data.snow_depth)}
                                                        </Typography>
                                                    )}
                                                    {data.seeing && (
                                                        <Typography variant="body2" color="text.secondary">
                                                            <Link href="https://en.wikipedia.org/wiki/Astronomical_seeing" target="_blank" rel="noopener noreferrer" color="inherit" underline="always">
                                                                <Typography component="span" fontWeight="bold">Astronomical Seeing</Typography>
                                                            </Link>
                                                            <Typography component="span" fontWeight="bold">{': '}</Typography>
                                                            {getSeeingType(data.seeing)}
                                                        </Typography>
                                                    )}
                                                    {data.transparency && (
                                                        <Typography variant="body2" color="text.secondary">
                                                            <Typography component="span" fontWeight="bold">Atmospheric Transparency:</Typography> {getTransparencyType(data.transparency)}
                                                        </Typography>
                                                    )}
                                                    {data.wind_profile && (
                                                        <Box component="div" sx={{ mb: 1 }}>
                                                            <Accordion sx={{ boxShadow: 'none', '&:before': { display: 'none' } }}>
                                                                <AccordionSummary
                                                                    expandIcon={<ExpandMoreIcon />}
                                                                    sx={{
                                                                        minHeight: '32px',
                                                                        padding: 0,
                                                                        '& .MuiAccordionSummary-content': {
                                                                            margin: '4px 0',
                                                                            padding: 0
                                                                        }
                                                                    }}
                                                                >
                                                                    <Typography component="span" fontWeight="bold">Wind Profile</Typography>
                                                                </AccordionSummary>
                                                                <AccordionDetails sx={{ pt: 0 }}>
                                                                    {data.wind_profile.map((wind, i) => (
                                                                        <Typography key={i} variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>
                                                                            {wind.layer}: {getWindSpeedText(wind.speed)} {' '}
                                                                            <span style={{ display: 'inline-block', transform: `rotate(${Number(wind.direction) - 90}deg)`, transformOrigin: 'center' }}>{getWindDirectionArrow()}</span>
                                                                        </Typography>
                                                                    ))}
                                                                </AccordionDetails>
                                                            </Accordion>
                                                        </Box>
                                                    )}
                                                    {data.rh_profile && (
                                                        <Box component="div" sx={{ mb: 1 }}>
                                                            <Accordion sx={{ boxShadow: 'none', '&:before': { display: 'none' } }}>
                                                                <AccordionSummary
                                                                    expandIcon={<ExpandMoreIcon />}
                                                                    sx={{
                                                                        minHeight: '32px',
                                                                        padding: 0,
                                                                        '& .MuiAccordionSummary-content': {
                                                                            margin: '4px 0',
                                                                            padding: 0
                                                                        }
                                                                    }}
                                                                >
                                                                    <Typography component="span" fontWeight="bold">Humidity Profile</Typography>
                                                                </AccordionSummary>
                                                                <AccordionDetails sx={{ pt: 0 }}>
                                                                    {data.rh_profile.map((rh, i) => (
                                                                        <Typography key={i} variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>
                                                                            {rh.layer}: {getRelativeHumidityText(rh.rh)}
                                                                        </Typography>
                                                                    ))}
                                                                </AccordionDetails>
                                                            </Accordion>
                                                        </Box>
                                                    )}
                                                </CardContent>
                                            </Card>
                                        </Grid>
                                    ))}
                                </Grid>
                            </>
                        );
                    })()}
                    <Box sx={{ mt: 4, textAlign: 'center' }}>
                        <Typography variant="body2" color="text.secondary">
                            Weather data provided by{' '}
                            <a
                                href="https://www.7timer.info/doc.php"
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{ color: 'inherit', textDecoration: 'underline' }}
                            >
                                7Timer!
                            </a>
                        </Typography>
                        <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mb: 1 }}>
                            Model-derived estimates may differ from the{' '}
                            <a
                                href={getNWSForecastUrl(location)}
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{ color: 'inherit', textDecoration: 'underline' }}
                            >
                                National Weather Service
                            </a>
                        </Typography>
                    </Box>
                </>
            )}
        </Box>
    );
};

export default Weather; 