export const getWeatherIcon = (weather: string) => {
    const icons: { [key: string]: string } = {
        'clearday': '☀️',
        'clearnight': '🌙',
        'pcloudyday': '🌤️',
        'pcloudynight': '☁️',
        'mcloudyday': '⛅',
        'mcloudynight': '☁️',
        'cloudyday': '☁️',
        'cloudynight': '☁️',
        'humidday': '💧',
        'humidnight': '💧',
        'lightrainday': '🌦️',
        'lightrainnight': '🌧️',
        'oshowerday': '🌦️',
        'oshowernight': '🌧️',
        'ishowerday': '🌦️',
        'ishowernight': '🌧️',
        'lightsnowday': '🌨️',
        'lightsnownight': '🌨️',
        'rainday': '🌧️',
        'rainnight': '🌧️',
        'snowday': '🌨️',
        'snownight': '🌨️',
        'rainsnowday': '🌨️',
        'rainsnownight': '🌨️',
        'tsday': '⛈️',
        'tsnight': '⛈️',
        'tsrainday': '⛈️',
        'tsrainnight': '⛈️',
    };
    return icons[weather] || '❓';
};

export const getCloudCoverText = (cover: number): string => {
    switch (cover) {
        case 1: return 'Clear (0%-6%)';
        case 2: return 'Mostly Clear (6%-19%)';
        case 3: return 'Partly Cloudy (19%-31%)';
        case 4: return 'Partly Cloudy (31%-44%)';
        case 5: return 'Partly Cloudy (44%-56%)';
        case 6: return 'Mostly Cloudy (56%-69%)';
        case 7: return 'Mostly Cloudy (69%-81%)';
        case 8: return 'Cloudy (81%-94%)';
        case 9: return 'Overcast (94%-100%)';
        default: return 'Unknown';
    }
};

export const getLiftedIndexText = (index: number): string => {
    switch (index) {
        case -10:
            return 'Below -7';
        case -6:
            return '-7 to -5';
        case -4:
            return '-5 to -3';
        case -1:
            return '-3 to 0';
        case 2:
            return '0 to 4';
        case 6:
            return '4 to 8';
        case 10:
            return '8 to 11';
        case 15:
            return 'Over 11';
        default:
            return 'Unknown';
    }
};

export const getSeeingType = (type: number): string => {
    switch (type) {
        case 1: return '<0.5"';
        case 2: return '0.5"-0.75"';
        case 3: return '0.75"-1"';
        case 4: return '1"-1.25"';
        case 5: return '1.25"-1.5"';
        case 6: return '1.5"-2"';
        case 7: return '2"-2.5"';
        case 8: return '>2.5"';
        default: return String(type);
    }
}

export const getTransparencyType = (type: number): string => {
    switch (type) {
        case 1: return '<0.3';
        case 2: return '0.3-0.4';
        case 3: return '0.4-0.5';
        case 4: return '0.5-0.6';
        case 5: return '0.6-0.7';
        case 6: return '0.7-0.85';
        case 7: return '0.85-1';
        case 8: return '>1';
        default: return String(type);
    }
}

export const getRelativeHumidityText = (type: number): string => {
    switch (type) {
        case -4: return '0%-5%';
        case -3: return '5%-10%';
        case -2: return '10%-15%';
        case -1: return '15%-20%';
        case 0: return '20%-25%';
        case 1: return '25%-30%';
        case 2: return '30%-35%';
        case 3: return '35%-40%';
        case 4: return '40%-45%';
        case 5: return '45%-50%';
        case 6: return '50%-55%';
        case 7: return '55%-60%';
        case 8: return '60%-65%';
        case 9: return '65%-70%';
        case 10: return '70%-75%';
        case 11: return '75%-80%';
        case 12: return '80%-85%';
        case 13: return '85%-90%';
        case 14: return '90%-95%';
        case 15: return '95%-99%';
        case 16: return '100%';
        default: return String(type);
    }
};

export const getWindSpeedText = (speed: number): string => {
    switch (speed) {
        case 1: return 'Below 0.3m/s (calm)';
        case 2: return '0.3-3.4m/s (light)';
        case 3: return '3.4-8.0m/s (moderate)';
        case 4: return '8.0-10.8m/s (fresh)';
        case 5: return '10.8-17.2m/s (strong)';
        case 6: return '17.2-24.5m/s (gale)';
        case 7: return '24.5-32.6m/s (storm)';
        case 8: return '32.6-36.7m/s (hurricane)';
        case 9: return '36.7-41.4m/s (hurricane+)';
        case 10: return '41.4-46.2m/s (hurricane+)';
        case 11: return '46.2-50.9m/s (hurricane+)';
        case 12: return '50.9-55.9m/s (hurricane+)';
        case 13: return 'Over 55.9m/s (hurricane+)';
        default: return 'Unknown';
    }
};

export const getWindDirectionArrow = (): string => {
    return `→`;
};


export const getPrecipitationType = (type: string): string => {
    const types: { [key: string]: string } = {
        'none': 'None',
        'rain': 'Rain',
        'snow': 'Snow',
        'rainsnow': 'Rain and Snow',
        'frzr': 'Freezing Rain',
        'icep': 'Ice Pellets',
        'frzs': 'Freezing Drizzle',
        'drizzle': 'Drizzle',
        'showers': 'Showers',
        'thunderstorm': 'Thunderstorm'
    };
    return types[type.toLowerCase()] || type;
};

export const getPrecipitationAmount = (amount: number): string => {
    switch (amount) {
        case 0: return 'None';
        case 1: return '0-0.25mm/hr';
        case 2: return '0.25-1mm/hr';
        case 3: return '1-4mm/hr';
        case 4: return '4-10mm/hr';
        case 5: return '10-16mm/hr';
        case 6: return '16-30mm/hr';
        case 7: return '30-50mm/hr';
        case 8: return '50-75mm/hr';
        case 9: return '75+mm/hr';
        default: return 'Unknown';
    }
};

export const getSnowDepthText = (depth: number): string => {
    switch (depth) {
        case 0: return 'None';
        case 1: return '0-1cm';
        case 2: return '1-5cm';
        case 3: return '5-10cm';
        case 4: return '10-25cm';
        case 5: return '25-50cm';
        case 6: return '50-100cm';
        case 7: return '100-150cm';
        case 8: return '150-250cm';
        case 9: return '250+cm';
        default: return 'Unknown';
    }
};