import { Location } from '../types/weather';

// Get timezone for a location based on coordinates
export const getTimezoneForLocation = (location: Location): string => {
    var tzlookup = require("tz-lookup");
    const timezone = tzlookup(location.lat, location.lng);
    return timezone;
};

export const getLocalTime = (date: Date, timepoint: number, location: Location): Date => {
    // Get timezone
    const timezone = getTimezoneForLocation(location);

    // Create a new date with the timepoint added
    const dateWithTimepoint = new Date(date.getTime() + (timepoint * 60 * 60 * 1000));

    // Convert to local time using the timezone
    return new Date(dateWithTimepoint.toLocaleString('en-US', { timeZone: timezone }));
};

export const formatTime = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleString('en-US', {
        ...options,
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
    });
};

export const formatDate = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleDateString('en-US', {
        ...options,
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
    });
};

export const formatShortDateTime = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleString('en-US', {
        ...options,
        month: 'short',
        day: 'numeric',
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
    });
};

export const formatDayAndHour = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleString('en-US', {
        ...options,
        weekday: 'short',
        hour: 'numeric',
        hour12: true
    });
};

export const formatDayOfWeek = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleDateString('en-US', {
        ...options,
        weekday: 'long'
    });
};

export const formatHour = (date: Date, options?: Intl.DateTimeFormatOptions): string => {
    return date.toLocaleString('en-US', {
        ...options,
        hour: '2-digit',
        hour12: true
    });
};