import { WeatherData, SevenTimerResponse, Location, NominatimResponse } from '../types/weather';
import { getLocalTime } from '../utils/timeUtils';

// Parse all init timestamps (format: YYYYMMDDHH)
const parseInit = (init: string): Date => {
    const year = parseInt(init.substring(0, 4));
    const month = parseInt(init.substring(4, 6)) - 1; // JavaScript months are 0-based
    const day = parseInt(init.substring(6, 8));
    const hour = parseInt(init.substring(8, 10));
    return new Date(Date.UTC(year, month, day, hour));
};

export const fetchWeatherData = async (location: Location): Promise<{ data: WeatherData[], date: Date }> => {
    // Fetch ASTRO, METEO, and CIVIL data
    const [astroResponse, meteoResponse, civilResponse] = await Promise.all([
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=astro&output=json`
        ),
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=meteo&output=json`
        ),
        fetch(
            `https://www.7timer.info/bin/api.pl?lon=${location.lng}&lat=${location.lat}&product=civil&output=json`
        )
    ]);

    if (!astroResponse.ok || !meteoResponse.ok || !civilResponse.ok) {
        throw new Error('Failed to fetch weather data');
    }

    // Parse the JSON responses
    const astroSevenTimerResponse: SevenTimerResponse = await astroResponse.json();
    const meteoSevenTimerResponse: SevenTimerResponse = await meteoResponse.json();
    const civilSevenTimerResponse: SevenTimerResponse = await civilResponse.json();

    console.log(astroSevenTimerResponse);
    console.log(meteoSevenTimerResponse);
    console.log(civilSevenTimerResponse);

    // Parse the init timestamps
    const astroDate = parseInit(astroSevenTimerResponse.init);
    const meteoDate = parseInit(meteoSevenTimerResponse.init);
    const civilDate = parseInit(civilSevenTimerResponse.init);

    // Convert the SevenTimerData to WeatherData
    const astroData = astroSevenTimerResponse.dataseries.map((astroItem) => ({
        ...astroItem,
        localDate: getLocalTime(astroDate, astroItem.timepoint, location)
    }));

    const meteoData = meteoSevenTimerResponse.dataseries.map((meteoItem) => ({
        ...meteoItem,
        localDate: getLocalTime(meteoDate, meteoItem.timepoint, location)
    }));

    const civilData = civilSevenTimerResponse.dataseries.map((civilItem) => ({
        ...civilItem,
        localDate: getLocalTime(civilDate, civilItem.timepoint, location)
    }));

    // Map of localtime's to dataseries
    const localTimeToAstroData = new Map<string, WeatherData>();
    const localTimeToMeteoData = new Map<string, WeatherData>();
    const localTimeToCivilData = new Map<string, WeatherData>();

    astroData.forEach((astroItem) => {
        localTimeToAstroData.set(astroItem.localDate.toISOString(), astroItem);
    });

    meteoData.forEach((meteoItem) => {
        localTimeToMeteoData.set(meteoItem.localDate.toISOString(), meteoItem);
    });

    civilData.forEach((civilItem) => {
        localTimeToCivilData.set(civilItem.localDate.toISOString(), civilItem);
    });

    // Combine the data from all three responses, adjusting indices based on time differences
    const data = Array.from(localTimeToMeteoData.entries()).map(([localTime, meteoItem]) => {
        const civilItem = localTimeToCivilData.get(localTime);
        const astroItem = localTimeToAstroData.get(localTime);
        if (civilItem) {
            // Extract the "weather" from civilItem and override
            meteoItem.weather = civilItem.weather;
        }
        if (astroItem) {
            // Extract the "seeing" and "transparency" from astroItem and override
            meteoItem.seeing = astroItem.seeing;
            meteoItem.transparency = astroItem.transparency;
        }
        return meteoItem;
    });

    return { data, date: meteoDate };
};

export const searchLocation = async (query: string): Promise<NominatimResponse[]> => {
    const response = await fetch(
        `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(
            query
        )}&limit=1`
    );
    return response.json();
}; 