export { };

// Conversion functions for plotting weather data
export const convertCloudCover = (cover: number): number => {
    // Convert cloud cover index to percentage (midpoint of range)
    switch (cover) {
        case 1: return 3;    // 0%-6%
        case 2: return 12.5; // 6%-19%
        case 3: return 25;   // 19%-31%
        case 4: return 37.5; // 31%-44%
        case 5: return 50;   // 44%-56%
        case 6: return 62.5; // 56%-69%
        case 7: return 75;   // 69%-81%
        case 8: return 87.5; // 81%-94%
        case 9: return 97;   // 94%-100%
        default: return 0;
    }
};

export const convertLiftedIndex = (index: number): number => {
    // Convert lifted index to numeric value (midpoint of range)
    switch (index) {
        case -10: return -8.5;  // Below -7
        case -6: return -6;     // -7 to -5
        case -4: return -4;     // -5 to -3
        case -1: return -1.5;   // -3 to 0
        case 2: return 2;       // 0 to 4
        case 6: return 6;       // 4 to 8
        case 10: return 9.5;    // 8 to 11
        case 15: return 13;     // Over 11
        default: return 0;
    }
};

export const convertSeeing = (type: number): number => {
    // Convert seeing type to numeric value (midpoint of range in arcseconds)
    switch (type) {
        case 1: return 0.25;  // <0.5"
        case 2: return 0.625; // 0.5"-0.75"
        case 3: return 0.875; // 0.75"-1"
        case 4: return 1.125; // 1"-1.25"
        case 5: return 1.375; // 1.25"-1.5"
        case 6: return 1.75;  // 1.5"-2"
        case 7: return 2.25;  // 2"-2.5"
        case 8: return 2.75;  // >2.5"
        default: return 0;
    }
};

export const convertSeeingBack = (value: number): number => {
    // Convert numeric value back to seeing type
    if (value < 0.5) return 1;
    if (value < 0.75) return 2;
    if (value < 1) return 3;
    if (value < 1.25) return 4;
    if (value < 1.5) return 5;
    if (value < 2) return 6;
    if (value < 2.5) return 7;
    return 8;
};

export const convertTransparency = (type: number): number => {
    // Convert transparency type to numeric value (midpoint of range)
    switch (type) {
        case 1: return 0.15;  // <0.3
        case 2: return 0.35;  // 0.3-0.4
        case 3: return 0.45;  // 0.4-0.5
        case 4: return 0.55;  // 0.5-0.6
        case 5: return 0.65;  // 0.6-0.7
        case 6: return 0.775; // 0.7-0.85
        case 7: return 0.925; // 0.85-1
        case 8: return 1.1;   // >1
        default: return 0;
    }
};

export const convertTransparencyBack = (value: number): number => {
    // Convert numeric value back to transparency type
    if (value < 0.3) return 1;
    if (value < 0.4) return 2;
    if (value < 0.5) return 3;
    if (value < 0.6) return 4;
    if (value < 0.7) return 5;
    if (value < 0.85) return 6;
    if (value < 1) return 7;
    return 8;
};

export const convertRelativeHumidity = (type: number): number => {
    // Convert relative humidity index to percentage (midpoint of range)
    switch (type) {
        case -4: return 2.5;   // 0%-5%
        case -3: return 7.5;   // 5%-10%
        case -2: return 12.5;  // 10%-15%
        case -1: return 17.5;  // 15%-20%
        case 0: return 22.5;   // 20%-25%
        case 1: return 27.5;   // 25%-30%
        case 2: return 32.5;   // 30%-35%
        case 3: return 37.5;   // 35%-40%
        case 4: return 42.5;   // 40%-45%
        case 5: return 47.5;   // 45%-50%
        case 6: return 52.5;   // 50%-55%
        case 7: return 57.5;   // 55%-60%
        case 8: return 62.5;   // 60%-65%
        case 9: return 67.5;   // 65%-70%
        case 10: return 72.5;  // 70%-75%
        case 11: return 77.5;  // 75%-80%
        case 12: return 82.5;  // 80%-85%
        case 13: return 87.5;  // 85%-90%
        case 14: return 92.5;  // 90%-95%
        case 15: return 97;    // 95%-99%
        case 16: return 100;   // 100%
        default: return 0;
    }
};

export const convertWindSpeed = (speed: number): number => {
    // Convert wind speed index to m/s (midpoint of range)
    switch (speed) {
        case 1: return 0.15;   // Below 0.3m/s
        case 2: return 1.85;   // 0.3-3.4m/s
        case 3: return 5.7;    // 3.4-8.0m/s
        case 4: return 9.4;    // 8.0-10.8m/s
        case 5: return 14;     // 10.8-17.2m/s
        case 6: return 20.85;  // 17.2-24.5m/s
        case 7: return 28.55;  // 24.5-32.6m/s
        case 8: return 34.65;  // 32.6-36.7m/s
        case 9: return 39.05;  // 36.7-41.4m/s
        case 10: return 43.8;  // 41.4-46.2m/s
        case 11: return 48.55; // 46.2-50.9m/s
        case 12: return 53.4;  // 50.9-55.9m/s
        case 13: return 58;    // Over 55.9m/s
        default: return 0;
    }
};

export const convertPrecipitationAmount = (amount: number): number => {
    // Convert precipitation amount index to mm/hr (midpoint of range)
    switch (amount) {
        case 0: return 0;      // None
        case 1: return 0.125;  // 0-0.25mm/hr
        case 2: return 0.625;  // 0.25-1mm/hr
        case 3: return 2.5;    // 1-4mm/hr
        case 4: return 7;      // 4-10mm/hr
        case 5: return 13;     // 10-16mm/hr
        case 6: return 23;     // 16-30mm/hr
        case 7: return 40;     // 30-50mm/hr
        case 8: return 62.5;   // 50-75mm/hr
        case 9: return 85;     // 75+mm/hr
        default: return 0;
    }
};

export const convertSnowDepth = (depth: number): number => {
    // Convert snow depth index to cm (midpoint of range)
    switch (depth) {
        case 0: return 0;      // None
        case 1: return 0.5;    // 0-1cm
        case 2: return 3;      // 1-5cm
        case 3: return 7.5;    // 5-10cm
        case 4: return 17.5;   // 10-25cm
        case 5: return 37.5;   // 25-50cm
        case 6: return 75;     // 50-100cm
        case 7: return 125;    // 100-150cm
        case 8: return 200;    // 150-250cm
        case 9: return 275;    // 250+cm
        default: return 0;
    }
};
